.infoBlock {
  display: flex;
  margin-bottom: 25px;
}
.iconBlock {
  margin-right: 20px;
  border-right: 2px solid #474444;
}
.icon {
  width: 50px;
  height: 50px;
  margin-right: 20px;
}
.title {
  font-size: 15px;
  font-weight: 600;
  margin-bottom: 0;
  color: #f0f0f0;
}
.content:hover {
  animation: link-decoration 3s linear infinite;
}

@keyframes link-decoration {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
